@import url('https://fonts.googleapis.com/css2?family=DM+Serif+Display&family=Karla:wght@400;700&display=swap');

$Dark-Violet: hsl(256, 26%, 20%);
$Grayish-Blue: hsl(216, 30%, 68%);
$Very-Dark-Violet: hsl(270, 9%, 17%);
$Dark-Grayish-Violet: hsl(273, 4%, 51%);
$Very-Light-Gray: hsl(0, 0%, 98%);

//font-family: 'DM Serif Display', serif;
// font-family: 'Karla', sans-serif;

*,
*::after,
*::before {
    margin: 0;
    padding: 0;
}

img {
    display: block;
    max-width: 100%;
}

body {

    // min-height: 100vh;
    background-color: $Very-Light-Gray;

    header {
        background-color: $Very-Light-Gray;
        display: flex;
        padding-block: 1em;
        padding-inline: 1em;
        // font-family: 'DM Serif Display', serif;
        font-weight: 400;
        position: relative;

        nav {
            position: absolute;
            top: 100%;
            left: 0;
            height: 90vh;
            background-color: $Very-Dark-Violet;
            width: 100%;
            background-image: url('../images/bg-pattern-mobile-nav.svg');
            background-position: 100% 100%;
            background-repeat: no-repeat;
            background-size: 100%;
            display: none;

            ul {
                text-align: center;
                margin-block-start: 4em;
                padding-inline: 2.5em;

                li {
                    margin-block: 3em;

                    a {
                        text-decoration: none;
                        color: $Very-Light-Gray;
                        font-family: 'Karla', sans-serif;
                        font-weight: 700;
                        font-size: 1.5rem;
                        letter-spacing: 3px;
                    }
                }

                .ViewPlanBorder {
                    border: 2px solid $Very-Light-Gray;
                    padding-block: 1em;
                }

            }

        }

        .Close-btn {
            display: none;
            // margin-left: auto
        }

        .Margin-left {
            margin-left: auto;
        }

        // .Open-btn {
            // display: none;
        // }
    }

    main {

        .firstSection {

            .MainPhoto {
                top: 0;
                width: 100%;
            }

            .IntroBox {
                padding-block: 6em;
                background-color: $Dark-Violet;
                color: $Very-Light-Gray;
                text-align: center;
                font-family: 'Karla', sans-serif;

                h1 {
                    font-family: 'DM Serif Display', serif;
                    font-size: 3rem;
                    letter-spacing: 1px;
                    font-weight: 400;
                }

                p {
                    margin-block: 2em 3em;
                    letter-spacing: 0.8px;
                    line-height: 1.5;
                }

                .ViewPlanaHref {
                    // border: 2px solid $Very-Light-Gray;
                    // padding-block: 0.7em;
                    // padding-inline: 2em;
                    // background-color: $Dark-Violet;
                    color: $Very-Light-Gray;
                }

            }
        }

        .SecondSection {

            padding-block: 4em;



            .CompanyDetailsBox {
                background-color: $Very-Light-Gray;
                text-align: center;
                // position: relative;



                .SecondHeader {
                    font-size: 2.5rem;
                    color: $Dark-Violet;
                }

                Article {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    margin-block: 1.5em;


                    .ArticleHeader {
                        color: $Dark-Violet;
                        margin-block: 0.8em;
                        font-size: 1.8rem;
                    }

                    .ArticleParagraph {
                        color: $Dark-Grayish-Violet;
                        word-spacing: 2px;
                        line-height: 1.5;
                    }
                }

            }
        }

        .ThirdSection {

            .advertisingBox {
                background-color: $Dark-Violet;
                color: $Very-Light-Gray;
                padding-block: 3em;
                padding-inline: 2em;
                text-align: center;

                .advertisingBoxParagraph {
                    font-size: 2.5em;
                    font-family: 'DM Serif Display', serif;
                    font-weight: 400;
                }
            }

        }
    }
}

.Container {
    // background-color: pink;
    padding-inline: 2em;
}

a {
    text-decoration: none;
}

li {
    list-style: none;
}

.anchorTags {
    border: 2px solid $Very-Light-Gray;
    color: $Very-Light-Gray;
}



@media (width>=786px) {

    body {
        background-color: red;
    }
}